@import "variables";

@mixin slant($position) {
  &::#{$position} {
    content: "";
    position: absolute;
    z-index: -1;
    left: 0;
    width: 100%;
    height: 100%;
    transform-origin: top left;
    background: inherit;
    transform: skewY($slant-angle);

    @if ($position == before) {
      top: -$slant-padding;
    } @else {
      top: 0;
    }
  }

  @media (min-width: $slant-intercept) {
    &::#{$position} {
      transform: skewY(2deg);
    }
  }
}
